"use client";
import {
  FaTelegramPlane,
  FaInstagram,
  FaFacebook,
  FaTiktok,
} from "react-icons/fa";
import Link from "next/link";
import RegisterForm from "@/components/auth";
import { createClient } from "@/utils/supabase/client";
import { useEffect, useState } from "react";
import { useRouter } from "next/router";

export default function Dashboard() {
  const menuItems = [
    { icon: FaTelegramPlane, label: "Telegram", path: "/dashboard/telegram" },
    { icon: FaInstagram, label: "Instagram", path: "/dashboard/instagram" },
    { icon: FaTiktok, label: "TikTok", path: "/dashboard/tiktok" },
    { icon: FaFacebook, label: "Facebook", path: "/dashboard/facebook" },
  ];

  const [authenticated, setAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);
  const router = useRouter(); // Навігація Next.js

  useEffect(() => {
    const getSession = async () => {
      const supabase = createClient();
      try {
        const {
          data: { session },
        } = await supabase.auth.getSession();

        if (session) {
          setAuthenticated(true);
        } else {
          setAuthenticated(false);
          router.push("/auth"); // Редирект на сторінку логіну
        }
      } catch (error) {
        console.error("Error fetching session:", error);
        setAuthenticated(false);
        router.push("/auth");
      } finally {
        setLoading(false);
      }
    };

    getSession();
  }, [router]);

  if (loading) {
    return <div>Loading...</div>; // Спінер або індикатор завантаження
  }

  if (!authenticated) {
    return <RegisterForm />;
  }

  return (
    <>
      <div>
        <h2 className="text-xl font-semibold mb-4">Dashboard</h2>
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {menuItems.map((item) => {
          const Icon = item.icon;
          return (
            <Link
              key={item.path}
              href={item.path}
              className="flex flex-col items-center justify-center w-full h-28 bg-blue-500 text-white hover:bg-blue-600 rounded-lg"
            >
              <div className="flex items-center gap-2">
                <Icon className="w-6 h-6" />
                <span>{item.label}</span>
              </div>
            </Link>
          );
        })}
      </div>
    </>
  );
}
